import { PageContainer } from "../../Components/Global/PageContainer";
import { ButtonRule, ButtonRuleContainer, Coringa, MemoryRulesContainer, Content } from './style.js'
import { LogoGame } from "../Menu/style.js";
import Play from '../../assets/Play.svg';

export default function MemoryRules() {
    return (
        <PageContainer>
            <MemoryRulesContainer>
                <Content>Este  o jogo da memria, encontre o par de cada carta, porm tome cuidado com o coringa,  a carta que voc no quer achar.</Content>
                <Content>Jogo da Memria</Content>
                <Coringa></Coringa>
                <Content>Vamos comear?</Content>
                <ButtonRuleContainer>
                    <ButtonRule to={"../gamememory"}>
                        <LogoGame src={Play} />PLAY
                    </ButtonRule>
                    <ButtonRule to={"/"}>
                        NO
                    </ButtonRule>
                </ButtonRuleContainer>
            </MemoryRulesContainer>
        </PageContainer>
    )
}